.journal__main-content{
    display: flex;
}

.journal__sidebar{
    background-color: $dark-grey;
    color: white;
    display: flex;
    flex-direction: column;
    height: 100vh;
    padding-left: 10px;
    padding-right: 10px;
    width: 450px;
}

.journal__sidebar-navbar{
    display: flex;
    justify-content: space-between;

    h3{
        font-weight: lighter;
    }
}


.journal__new-entry{
    align-items: center;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 30px;
    width: 100%;

    transition: color .3s ease;

    &:hover{
        color: darken($color: white, $amount: 20);
    }
}


.journal__entries{
    margin-top: 30px;
    flex:  1 1 auto;
    overflow-y: scroll ;
}

.journal__entry{
    background-color: white;
    border-radius: 4px;
    color: $dark-grey;
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    margin-right: 5px;
    overflow: hidden;
}

.journal__entry-picture{
    height: 75px;
    width: 90px;
}

.journal__entry-body{
    padding: 10px;

}
.journal__entry-tittle{
    font-size: 14px;
    font-weight: bold;
}
.journal__entry-content{
    font-size: 10px;
    text-align: justify;
}

.journal__entry-date-box{
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 5px;
}

.journal__entry-date-box span{
    font-size: 12px;
}
